/*------------------------------------*\
    商品特色區塊 */
/*------------------------------------*\ */

.feature {

    // 經典系列商品
    &-classic {

        &-list {
            display: flex;
            margin-top: 48px;

            @include respond-to('phone') {
                flex-wrap: wrap; }

            @include respond-to('sphone') {
                width: 100%; } }

        &-item {
            margin-right: 24px;
            &:last-child {
                margin-right: 0; }

            @include respond-to('phone') {
                @include flexGrid(flex-start,center,column);
                width: 100%; }

            @include respond-to('sphone') {
                margin-right: 0; } }

        &-img {
            margin-bottom: 8px;

            @include respond-to('sphone') {
                width: 100%; } } }

    // 聯名設計商品
    &-signed {
        @include backgroundCover('../images/bg.png');

        &-list {
            display: flex;
            margin-top: 48px;
            @include size(100%,auto);

            @include respond-to('phone') {
                flex-wrap: wrap; } }

        &-item {
            @include size(100%);
            cursor: pointer;
            >h4 {
                transition: .3s; }

            &:hover {
                >h4 {
                    background-color: $color-primary;
                    color: #fff; } }

            @include respond-to('phone') {
                &:first-child {
                    margin-bottom: 24px; } } }

        &-img {
            @include size(100%, 240px); } } }

// 商品圖片
.woman-img {
    @include backgroundCover('../images/product-8.png'); }

.glasses-img {
    @include backgroundCover('../images/product-9.png'); }
