/*------------------------------------*\
    首頁公司特色 */
/*------------------------------------*\ */

.company-info {

    &-list {
        @include flexGrid(center,flex-start);
        margin-top: 32px;

        @include respond-to('phone') {
            flex-wrap: wrap; } }

    &-item {
        width: 25%;
        margin-right: 24px;
        @include flexGrid(center,center,column);
        &:last-child {
            margin-right: 0; }

        @include respond-to('phone') {
            width: 48%;
            &:nth-child(2n) {
                margin-right: 0; } }
        @include respond-to('sphone') {
            width: 100%;
            margin: 0 0 32px 0; } }

    // 特色內容
    &-img {
        @include size(83px);
        margin-bottom: 24px; }

    &-title {
        @include fontSize(24px,36px,bold);
        color: $color-third;
        margin-bottom: 8px; }

    &-word {
        @include fontSize(16px,24px); } }
