/*------------------------------------*\
    共用區塊設定 */
/*------------------------------------*\ */

.section {
    // 首頁樣式

    &-index {
        padding: 80px 312px;
        @include flexGrid(center,center,column);
        @include respond-to('phone') {
            padding: 80px 36px; }
        @include respond-to('sphone') {
            padding: 40px 13px; }

        &-form {
            background-color: $color-secondary;
            @include flexGrid(center,center,column);
            @include respond-to('phone') {
                padding: 80px 96px; }
            @include respond-to('sphone') {
                padding: 40px 13px; } }

        // 共用標題
        &-title {
            @include fontSize(48px,72px,bold);
            text-align: center;
            &.red {
                color: $color-third; }
            &.white {
                color: $color-white; } }

        // 底色區塊
        &.fill {
            background-color: $color-secondary; }

        // 共用副標題
        &-subtitle {
            @include fontSize(48px,72px,bold);
            color: $color-primary;
            background-color: $color-white;
            text-align: center; }

        // 共用文字樣式
        &-word {
            @include fontSize(20px,30px);
            width: 636px;

            @include respond-to('phone') {
                width: 100%; } } } }
