/*------------------------------------*\
    footer 設定 */
/*------------------------------------*\ */

.footer {
    padding: 48px 0 32px 0;
    background-color: $color-primary;

    @include respond-to('sphone') {
        padding: 24px 0; }

    // footer 導覽列
    &-list {
        @include flexGrid(space-between); }

    &-menu {
        @include fontSize(16px, 24px);
        display: flex;
        &-link {
            color: $color-white;
            margin-right: 24px;
            position: relative;
            @extend .link-border-animation; } }

    // 最下層 cpoyright 相關
    &-copyright {
        @include flexGrid(space-between);
        margin-top: 32px;
        color: $color-white;

        @include respond-to('sphone') {
            @include flexGrid(flex-start,flex-start,column);
            margin-top: 16px; }

        &-menu {
            display: flex;

            @include respond-to('sphone') {
                flex-wrap: wrap; } }

        &-item {
            margin-right: 48px;
            &:last-child {
                margin-right: 0; }

            @include respond-to('sphone') {
                width: 100%;
                margin: 8px 0 0 0; } }

        &-link {
            color: $color-white;
            position: relative;
            @extend .link-border-animation; } }

    &-lg {
        @include respond-to('sphone') {
            display: none; } }

    &-sm {
        display: none;
        @include respond-to('sphone') {
            display: block; } } }

// 社群按鈕
.social-menu {
    display: flex;

    &-item {
        margin-right: 24px;
        &:last-child {
            margin-right: 0; }

        @include respond-to('sphone') {
            @include size(24px); } } }

// 聯絡方式
.contact-list {
    padding: 35px 0;
    border-bottom: 1px solid $color-white;

    @include respond-to('sphone') {
        padding: 0 0 16px 0; }

    &-item {
        display: flex;
        align-items: center; }
    &-img {
        margin-right: 20px;
        @include size(36px);

        @include respond-to('sphone') {
            @include size(24px); } }
    &-link {
        @include fontSize(36px,54px);
        color: $color-white;
        transition: .3s;
        &:hover {
            color: #ffba08; } } }
