/*------------------------------------*\
    顧客推薦設定 */
/*------------------------------------*\ */

.recommend {

    &-list {
        @include flexGrid(space-between);
        margin-top: 48px;

        @include respond-to('phone') {
            flex-wrap: wrap; }

        &-item {
            @include size(25%, 511px);
            border-radius: 4px;
            overflow: hidden;
            box-shadow: 0 2px 6px #00000029;
            margin-right: 24px;

            @include respond-to('phone') {
                @include size(46%, 511px);
                &:nth-child(1),
                &:nth-child(2) {
                    margin-bottom: 24px; } }

            @include respond-to('sphone') {
                @include size(100%, 511px);
                margin: 0 0 24px 0;
                &:last-child {
                    margin-bottom: 0; } } } }

    &-img {
        @include size(100%, 50%); }

    &-content {
        padding: 16px;
        height: 50%;
        box-sizing: border-box;
        @include flexGrid(space-between,flex-start,column);
        @include fontSize(16px,24px); }

    &-text {
        display: block; }
    &-date {
        color: $color-darkGary; } }

/* ------------------- 圖片設定 ------------------- */

@for $i from 1 through 4 {
    .recommend-img {
        &.customer-0#{$i} {
            @include backgroundCover('../images/rec-#{$i}.png'); } } }
